:root {
  --background: #ffffff;
  --foreground: #171717;
  --font-primary: 'DM Sans', -apple-system, BlinkMacSystemFont, sans-serif;
  --font-secondary: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  --font-size-h1: 36px;
  --font-size-h2: 30px;
  --font-size-h3: 24px;
  --font-size-h4: 20px;
  --font-size-body-large: 18px;
  --font-size-body: 16px;
  --font-size-small: 14px;
  --font-size-caption: 12px;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-semibold: 600;
  --font-weight-bold: 700;
  --space-1: 4px;
  --space-2: 8px;
  --space-3: 12px;
  --space-4: 16px;
  --space-6: 24px;
  --space-8: 32px;
  --space-12: 48px;
  --space-16: 64px;
  --space-24: 96px;
  --space-32: 128px;
  --radius-small: 8px;
  --radius-medium: 12px;
  --radius-large: 16px;
  --radius-full: 9999px;
  --transition-fast: 150ms ease-out;
  --transition-default: 200ms ease-in-out;
  --transition-slow: 300ms ease-in-out;
  --transition-smooth: 400ms cubic-bezier(0.4, 0, 0.2, 1);
  --opacity-disabled: 0.4;
  --opacity-hover: 0.8;
  --opacity-overlay: 0.5;
  --opacity-subtle: 0.6;
  --container-max-width: 1280px;
  --grid-gap: var(--space-6);
}
:root,
.light {
  --color-bg-primary: #FEFEFE;
  --color-bg-secondary: #F3F4F6;
  --color-surface: #FFFFFF;
  --color-accent-primary: #D4654F;
  --color-accent-hover: #E08A78;
  --color-accent-light: #F5D5C8;
  --color-text-on-primary: #FFFFFF;
  --color-text-primary: #3F3D42;
  --color-text-secondary: #8B8A8F;
  --color-text-tertiary: #B5B4B8;
  --color-border: #E5E7EB;
  --shadow-small: 0 2px 8px rgba(0, 0, 0, 0.08);
  --shadow-medium: 0 4px 16px rgba(0, 0, 0, 0.12);
  --shadow-large: 0 8px 32px rgba(0, 0, 0, 0.16);
  --shadow-focus: 0 0 0 3px rgba(212, 101, 79, 0.2);
  --glow-accent: none;
}
.dark {
  --color-bg-primary: #09090B;
  --color-bg-secondary: #18181B;
  --color-surface: #27272A;
  --color-accent-primary: #D4654F;
  --color-accent-hover: #E08A78;
  --color-accent-light: #F5D5C8;
  --color-accent-secondary: rgba(212, 101, 79, 0.15);
  --color-text-on-primary: #FFFFFF;
  --color-text-primary: #FAFAFA;
  --color-text-secondary: #A1A1AA;
  --color-text-tertiary: #71717A;
  --color-border: rgba(255, 255, 255, 0.08);
  --shadow-small: 0 2px 8px rgba(0, 0, 0, 0.4);
  --shadow-medium: 0 4px 16px rgba(0, 0, 0, 0.5);
  --shadow-large: 0 8px 32px rgba(0, 0, 0, 0.6);
  --shadow-focus: 0 0 0 3px rgba(212, 101, 79, 0.3);
  --glow-accent: 0 0 20px rgba(212, 101, 79, 0.2);
}
@media (max-width: 767px) {
  :root {
    --font-size-h1: 48px;
    --font-size-h2: 36px;
    --font-size-h3: 28px;
    --font-size-h4: 20px;
    --font-size-body-large: 17px;
    --grid-gap: var(--space-4);
  }
}
[data-theme="warm"] {
  --color-bg-primary: #FEFEFE;
  --color-bg-secondary: #F3F4F6;
  --color-surface: #FFFFFF;
  --color-accent-primary: #D4654F;
  --color-accent-hover: #E08A78;
  --color-accent-light: #F5D5C8;
  --color-text-on-primary: #FFFFFF;
  --color-text-primary: #3F3D42;
  --color-text-secondary: #8B8A8F;
  --color-text-tertiary: #B5B4B8;
  --color-border: #E5E7EB;
  --shadow-small: 0 2px 8px rgba(0, 0, 0, 0.08);
  --shadow-medium: 0 4px 16px rgba(0, 0, 0, 0.12);
  --shadow-large: 0 8px 32px rgba(0, 0, 0, 0.16);
  --shadow-focus: 0 0 0 3px rgba(212, 101, 79, 0.2);
  --glow-accent: none;
}
body {
  background: var(--color-bg-primary);
  color: var(--color-text-primary);
  font-family: var(--font-secondary);
  line-height: var(--line-height-relaxed);
}
h1 {
  font-family: var(--font-primary);
  font-size: var(--font-size-h1);
  font-weight: var(--font-weight-bold);
  line-height: 1.2;
  letter-spacing: -0.02em;
}
h2 {
  font-family: var(--font-primary);
  font-size: var(--font-size-h2);
  font-weight: var(--font-weight-medium);
  line-height: 1.3;
  letter-spacing: -0.01em;
}
h3 {
  font-family: var(--font-primary);
  font-size: var(--font-size-h3);
  font-weight: var(--font-weight-medium);
  line-height: 1.4;
}
h4 {
  font-family: var(--font-primary);
  font-size: var(--font-size-h4);
  font-weight: var(--font-weight-medium);
  line-height: 1.5;
}
.glass-effect {
  background: rgba(255, 255, 255, 0.7);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.5);
}
.glass-effect-dark {
  background: rgba(30, 30, 30, 0.4);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(255, 255, 255, 0.05);
}
.glow-accent {
  box-shadow: var(--glow-accent);
}
[data-theme="twilight"] .glass-effect,
[data-theme="dusk"] .glass-effect {
  background: rgba(36, 45, 60, 0.6);
  border-color: rgba(255, 255, 255, 0.1);
}
